<template lang="html">
  <div>
    <div>
      <a
        class="pagination-link"
        :class="{'is-current': index === defaultPid}"
        v-for="(pid, index) in contest.list"
        @click="chooseProblem(index)"
      >{{ index }}</a>
    </div>
    <br>
    <oj-problemcontent
      :problem="problem"
    >
    <span slot="title"> {{pid}} -- {{problem.title}} </span>
    </oj-problemcontent>
    <hr>
    <button
      class="button is-primary" @click="submit"
      :disabled="currentTime <= contest.start || currentTime >= contest.end"
    >Submit</button>
    <p v-if="currentTime <= contest.start || currentTime >= contest.end">
      This contest is not on running, so you can't submit now.
    </p>
    <oj-submitcodemodal
      v-if="active"
      @close="active = false"
      @submit="submitCode">
      {{ this.pid }} -- {{ this.problem.title }}
    </oj-submitcodemodal>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

import ProblemContent from '../../components/ProblemContent.vue'
import SubmitCodeModal from '../../components/SubmitCodeModal.vue'

export default {
  components: {
    'oj-problemcontent': ProblemContent,
    'oj-submitcodemodal': SubmitCodeModal
  },
  props: ['cid', 'pid', 'contest'],
  data () {
    return {
      active: false
    }
  },
  computed: {
    ...mapGetters({
      defaultPid: 'contestDefaultPid',
      problem: 'problem',
      currentTime: 'currentTime'
    })
  },
  created () {
    this.$store.dispatch('fetchProblem', {
      pid: this.contest.list[this.pid],
      mid: this.cid
    })
    this.$store.commit('updateContestDefaultPid', {
      pid: +this.pid // 
    })
  },
  methods: {
    submit (problem) {
      this.active = true
    },
    submitCode (payload) {
      payload.mid = this.cid
      payload.pid = this.problem.pid
      this.$store.dispatch('submitSolution', payload)
        .then(() => {
          this.$router.push({
            name: 'contest_status'
          })
          this.active = false
        })
    },
    chooseProblem (index) {
      this.$store.commit('updateContestDefaultPid', {
        pid: index
      })
      this.$router.push({
        name: 'contest_problem',
        params: {
          cid: this.cid,
          pid: +index
        }
      })
      this.$store.dispatch('fetchProblem', {
        pid: +this.contest.list[index],
        mid: this.cid
      })
    }
  }
}
</script>

<style lang="css">
</style>



