<template lang="html">
  <div class="contest-ranklist">
    <table class="table is-bordered">
      <thead>
        <tr>
          <th>#</th> <th></th> <th>Nick</th> <th>Solve</th> <th>Penalty</th>
          <th v-for="(pid, index) in contest.list"> {{ index }} </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(rank, index) in ranklist"
        >
          <td>{{ index + 1 }}</td>
          <td>{{ rank.uid }}</td>
          <td>{{ rank.nick }}</td>
          <td>{{ rank.solve }}</td>
          <td>{{ rank.penalty | durationPretty }}</td>
          <template
            v-for="pid in contest.list"
          >
            <td v-if="typeof rank.solved[pid] === 'undefined'"></td>
            <td v-else-if="rank.solved[pid].wa >= 0"
                class="is-success"
                :class="{'is-first': rank.solved[pid].isFirst}"
              >
              {{ rank.solved[pid].create | durationPretty }}
              {{ rank.solved[pid].wa === 0 ? '' : `(${rank.solved[pid].wa})` }}
            </td>
            <td v-else class="is-danger">
              {{ rank.solved[pid].wa }}
            </td>
          </template>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: ['cid', 'contest'],
  created () {
    this.$store.dispatch('fetchContestRanklist', {
      cid: this.cid
    })
  },
  computed: {
    ...mapGetters({ ranklist: 'contestRanklist' })
  }
}
</script>

<style lang="css">
</style>



