<template lang="html">
  <div>
    <div class="field is-horizontal">
      <div class="field-label is-normal">
        <label class="label">User</label>
      </div>
      <div class="field-body">
        <div class="field">
          <p class="control is-expanded">
            <input class="input" type="text" placeholder="Username" v-model="searchUid">
          </p>
        </div>
        <div class="field-label is-normal">
          <label class="label">Pid</label>
        </div>
        <div class="field">
          <p class="control is-expanded">
            <input class="input" type="text" placeholder="Problem id" v-model="searchPid">
          </p>
        </div>
        <div class="field-label is-normal">
          <label class="label">Judge</label>
        </div>
        <div class="field">
          <p class="control is-expanded has-icons-left has-icons-right">
            <div class="select">
              <select v-model="searchJudge">
                <option :value="undefined">ALL</option>
                <option
                  v-for="(name, code) in JUDGES"
                  :value="code">{{ name }}</option>
              </select>
            </div>
          </p>
        </div>
        <div class="field-label is-normal">
          <label class="label">Language</label>
        </div>
        <div class="field">
          <p class="control is-expanded has-icons-left has-icons-right">
            <div class="select">
              <select v-model="searchLanguage">
                <option :value="undefined">ALL</option>
                <option
                  v-for="(name, code) in LANGUAGES"
                  :value="code">{{ name }}</option>
              </select>
            </div>
          </p>
        </div>
        <div class="field">
          <button class="button is-primary" @click="search">Search</button>
        </div>
      </div>
    </div>
    <oj-pagination
      :pagination="pagination"
      @pageClick="pageClick"
    ></oj-pagination>
    <table class="table">
      <thead>
        <tr>
          <th>SID</th>
          <th>PID</th>
          <th>Username</th>
          <th>Judge</th>
          <th>Time</th>
          <th>Memory</th>
          <th>Language</th>
          <th>Length</th>
          <th>Submit Time</th>
        </tr>
      </thead>
      <tfoot>
        <tr>
          <th>SID</th>
          <th>PID</th>
          <th>Username</th>
          <th>Judge</th>
          <th>Time</th>
          <th>Memory</th>
          <th>Language</th>
          <th>Length</th>
          <th>Submit Time</th>
        </tr>
      </tfoot>
      <tbody>
        <tr
          v-for="solution in solutions"
          :key="solution.sid"
          is="oj-solutionitem"
          :solution="solution"
        >
          <router-link
            slot="pid"
            :to="{name: 'contest_problem', params: {cid: cid, pid: problemIndex(solution.pid)}}"
          >
          {{ problemIndex(solution.pid) }}
          </router-link>
        </tr>
      </tbody>
    </table>
    <oj-pagination
      :pagination="pagination"
      @pageClick="pageClick"
    ></oj-pagination>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

import SolutionItem from '../../components/SolutionItem.vue'
import Pagination from '../../components/Pagination.vue'

export default {
  data () {
    //  null post  ?searchLanguage=&searchUid=
    return {
      searchUid: undefined,
      searchPid: undefined,
      searchJudge: undefined,
      searchLanguage: undefined
    }
  },
  components: {
    'oj-pagination': Pagination,
    'oj-solutionitem': SolutionItem
  },
  props: [ 'page', 'limit', 'judge', 'language', 'uid', 'pid', 'cid', 'contest' ],
  computed: {
    ...mapGetters({
      solutions: 'solutionsList',
      pagination: 'solutionsPagination',
      JUDGES: 'judges',
      LANGUAGES: 'languages'
    })
  },
  created () {
    this.$store.dispatch('fetchSolutionsList', {
      page: this.page,
      limit: this.limit,
      uid: this.uid,
      pid: this.contest.list[this.searchPid],
      language: this.language,
      judge: this.judge,
      mid: this.cid
    })
    this.updateSearch()
  },
  methods: {
    problemIndex (pid) {
      return this.contest.list.indexOf(pid)
    },
    pageClick (page) {
      //  url  query
      const query = {
        page,
        limit: this.limit,
        uid: this.uid,
        pid: this.contest.list[this.searchPid],
        language: this.language,
        judge: this.judge
      }
      this.$router.push({
        name: 'contest_status',
        query
      })
      //  dispatch
      // https://router.vuejs.org/en/essentials/dynamic-matching.html
      this.$store.dispatch('fetchSolutionsList', Object.assign(query, {
        mid: this.cid
      }))
    },
    search () {
      //  search  query
      const query = {
        page: 1,
        limit: this.limit,
        uid: this.searchUid === '' ? undefined : this.searchUid,
        pid: this.searchPid === '' ? undefined : this.contest.list[this.searchPid],
        language: this.searchLanguage,
        judge: this.searchJudge
      }
      this.$router.push({
        name: 'contest_status',
        query
      })
      //  dispatch
      // https://router.vuejs.org/en/essentials/dynamic-matching.html
      //  mid 
      this.$store.dispatch('fetchSolutionsList', Object.assign(query, {
        mid: this.cid
      }))
    },
    // 
    updateSearch () {
      this.searchUid = this.uid
      this.searchPid = this.pid
      this.searchJudge = this.judge
      this.language = this.language
    }
  }
}
</script>

<style lang="css">
</style>



